<script>
export default {
  name: "MyFAQ",
  components: {},
  data() {
    return { itemList: [] };
  },
  created() {
    this.getItemList();
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    getItemList() {
      this.$apiGET("/api/cs?typeNum=1").then(({ data }) => {
        for (let i = 0; i < data.length; i++) {
          data[i].state = false;
        }
        this.itemList = data;
      });
    },
  },
};
