<script>
export default {
  name: "MyPayDetail",
  components: {},
  data() {
    return {
      item: {
        PayDateTime: "",
        ProductName: "",
        Price: "",
        ProductNo: "",
        UserName: "",
        UserContact: "",
        Status: 0,
      },
      popupState: false,
    };
  },
  filters: {
    comma(val) {
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  created() {
    this.getItem();
  },
  methods: {
    getItem() {
      this.$apiGET(
        "/api/pay/breakdown/detail?merchantId=" + this.$route.query.id
      ).then(({ data }) => {
        this.item = data;
      });
    },
    goBack() {
      this.$router.go(-1);
    },
    goMenu(dest) {
      this.$router.push({
        name: dest,
        query: { id: this.$route.query.id },
      });
    },
  },
};
</script>
