<template>
  <div class="content">
    <div class="content-top center">
      <h2 class="tit"> </h2>
      <p class="sub">  </p>
    </div>
    <div class="input-wrap">
      <input
        type="password"
        class="input-text"
        placeholder="  (8  , ,  )"
        v-model="Password"
      />
    </div>
    <div class="input-wrap">
      <input
        type="password"
        class="input-text"
        placeholder="  "
        v-model="Password_"
      />
      <template v-if="Password && Password_">
        <p v-if="Password != Password_" class="guide">
            .
        </p>
      </template>
    </div>
    <div class="btn-wrap">
      <button type="button" class="btn btn-full btn-black" @click="goResult">
        
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "FindPassResult",
  components: {},
  data() {
    return {
      Password: "",
      Password_: "",
    };
  },
  methods: {
    goResult() {
      this.$apiPOST("/api/user/find/change/password", {
        NewPassword: this.Password,
      }).then(({ data }) => {
        this.$store.commit("updateError", data.message);
        this.$router.push({
          name: "Login",
        });
      });

      // this.$router.push({
      //   name: "Login",
      //   // query: { name: "Query  ", age: 2 },
      // });
    },
  },
};
</script>
