<script>
// import ContentList from "@/components/home/ContentList.vue";
// import axios from "axios";
// import InfiniteLoading from "vue-infinite-loading";
import VueSlickCarousel from "vue-slick-carousel";
// const api = "//hn.algolia.com/api/v1/search_by_date?tags=story";

export default {
  name: "Home",
  components: {
    // ContentList,
    // InfiniteLoading,
    VueSlickCarousel,
  },
  data() {
    return {
      // page: 1,
      // list: [],
      isCustom: "false",
      itemList: [],
      settings: {
        arrows: false,
        dots: true,
        autoplay: true,
        autoplaySpeed: 2500,
        // edgeFriction: 0.35,
        // infinite: false,
        // speed: 500,
        // slidesToShow: 1,
        // slidesToScroll: 1,
      },
      popupState: this.$store.state.isSub,
      user: {
        Name: "",
        Contact: "",
        RoadAddress: "",
        JibunAddress: "",
        ExtraAddress: "",
        PostCode: "",
        AddressType: "1",
      },
      Address: "", //
      con1: null,
      con2: null,
      con3: null,
      popupState2: false,
      popupState3: this.$store.state.isReview,
      popupState4: false,
    };
  },
  created() {
    this.getItem();
  },
  methods: {
    goNewPage(page) {
      window.open(page);
    },
    changeCustom() {
      this.$apiGET("/api/check/custom").then(({ data }) => {
        if (data.NeedReg && data.NeedSub && data.NeedBodyType) {
          // alert(" ");
          this.isCustom = "false";
          this.popupState2 = true;
        } else if (!data.NeedReg && data.NeedSub && data.NeedBodyType) {
          // alert(" ");
          this.isCustom = "false";
          this.popupState2 = true;
        } else if (!data.NeedReg && !data.NeedSub && data.NeedBodyType) {
          // alert(" ");
          this.isCustom = "false";
          this.popupState4 = true;
        } else {
          this.getItem();
        }
      });
    },
    getItem() {
      this.$apiGET("/api/home/contents?isCustom=" + this.isCustom).then(
        ({ data }) => {
          this.itemList = data;
        }
      );
    },
    goDetail(id) {
      this.$router.push({
        name: "Detail",
        query: { contentId: id },
      });
    },
    execDaumPostcode() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          if (this.user.ExtraAddress !== "") {
            this.user.ExtraAddress = "";
          }
          if (data.userSelectedType === "R") {
            //     
            this.Address = data.roadAddress;
            this.user.AddressType = "";
          } else {
            //     (J)
            this.Address = data.jibunAddress;
            this.user.AddressType = "";
          }
          this.user.RoadAddress = data.roadAddress;
          this.user.JibunAddress = data.jibunAddress;

          //       .
          if (data.userSelectedType === "R") {
            //    . ( )
            //     "//" .
            if (data.bname !== "" && /[||]$/g.test(data.bname)) {
              this.user.ExtraAddress += data.bname;
            }
            //  ,   .
            if (data.buildingName !== "" && data.apartment === "Y") {
              this.user.ExtraAddress +=
                this.user.ExtraAddress !== ""
                  ? `, ${data.buildingName}`
                  : data.buildingName;
            }
            //    ,     .
            if (this.user.ExtraAddress !== "") {
              this.user.ExtraAddress = `(${this.user.ExtraAddress})`;
            }
          } else {
            this.user.ExtraAddress = "";
          }
          //  .
          this.user.PostCode = data.zonecode;
        },
      }).open();
    },
    sendAddress() {
      this.user.Contact = this.con1 + "-" + this.con2 + "-" + this.con3;
      this.$apiPOST("/api/set/bodytype/deliver", this.user).then(() => {
        this.popupState = false;
      });
    },
    goMenu(dest) {
      this.$router.push({
        name: dest,
      });
    },
    goSub() {
      if (this.$cookies.get("FL") != null) {
        this.goMenu("MySub");
      } else {
        this.goMenu("Join");
      }
    },
  },
};
</script>
