<template>
  <div>
    <div class="content">
      <div class="content-tit">
        <button type="button" class="btn back" @click="goBack"></button>
          
      </div>

      <ul class="pay-list">
        <li class="pay-list--item">
          <div class="top">
            <a>{{ $date(item.PayDateTime).format("YYYY/MM/DD") }}</a>
          </div>
          <div class="con">
            <div class="left">
              <p class="tit">{{ item.ProductName }}</p>
              <p class="code">{{ item.MOId }}</p>
            </div>
            <div class="right">
              <p class="price">
                <strong>
                  {{ item.Price | comma }}
                </strong>
                
              </p>
              <p>
                <template v-if="item.Status == -1">  </template>
                <template v-else-if="item.Status == 0">  </template>
                <template v-else-if="item.Status == 2">  </template>
              </p>
            </div>
          </div>
        </li>
      </ul>

      <div class="line full grey m-t--0"></div>

      <div class="content-top">
        <h3 class="sub m-b--20"> </h3>
      </div>
      <div class="checkbox-list m-b--60">
        <label class="checkbox">
          <input
            type="radio"
            v-model="item.CancelType"
            value=" "
            :disabled="item.Status != 0"
          />
          <span class="check type2"></span>
          <span class="text"> </span>
        </label>
        <label class="checkbox">
          <input
            type="radio"
            v-model="item.CancelType"
            value=" "
            :disabled="item.Status != 0"
          />
          <span class="check type2"></span>
          <span class="text"> </span>
        </label>
        <label class="checkbox">
          <input
            type="radio"
            v-model="item.CancelType"
            value=" "
            :disabled="item.Status != 0"
          />
          <span class="check type2"></span>
          <span class="text"> </span>
        </label>
        <label class="checkbox">
          <input
            type="radio"
            v-model="item.CancelType"
            value=" "
            :disabled="item.Status != 0"
          />
          <span class="check type2"></span>
          <span class="text"> </span>
        </label>
        <div class="textarea-wrap">
          <textarea
            class="textarea"
            placeholder=" "
            rows="3"
            v-model="item.CancelDetail"
            :disabled="item.CancelType != ' ' || item.Status != 0"
          ></textarea>
        </div>
      </div>

      <div class="content-top">
        <h3 class="sub m-b--20">  </h3>
      </div>
      <ul class="text-list p-b--60">
        <li class="text-list--item">
          <span class="left"> </span>
          <span class="right">{{ item.Price | comma }}</span>
        </li>
        <li class="text-list--item">
          <span class="left"> </span>
          <span class="right font-color--primary"
            ><span class="font-color--grey font-weight--b font-size--10 m-r--10"
              >53% SAVE</span
            >
            0</span
          >
        </li>
        <li class="text-list--item">
          <span class="left"> </span>
          <span class="right">{{ item.Price | comma }}</span>
        </li>
      </ul>
    </div>

    <button
      v-if="item.Status == 0"
      type="button"
      class="btn bottom-fix btn-d-grey"
      @click="payCancel"
    >
       
    </button>
    <button
      v-else-if="item.Status == -1"
      type="button"
      class="btn bottom-fix btn-d-grey"
      @click="payCancelCancel"
    >
        
    </button>
    <div class="popup" v-bind:class="{ active: popupState }">
      <div class="popup-dim" @click="popupState = false"></div>
      <div class="popup-wrap">
        <div class="popup-tit">
          <button
            type="button"
            class="btn close"
            @click="popupState = false"
          ></button>
        </div>
        <div class="popup-con">
          <div class="content-top center">
            <p class="sub type2">
                 {{ viewCount }}   <br />
                  .<br />
              ,     <br />
                  .<br />
                 ?<br />
                  <br />
                <br />
            </p>
          </div>
        </div>
        <button type="button" class="btn close" @click="popupState = false">
          []
        </button>
        <button type="button" class="btn close" @click="payRealCancel(2)">
          [  ]
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyPayCancel",
  components: {},
  data() {
    return {
      item: {
        PayDateTime: "",
        ProductName: "",
        Price: "",
        ProductNo: "",
        UserName: "",
        UserContact: "",
        Status: 0,
        CancelType: "",
        CancelDetail: "",
      },
      viewCount: null,
      popupState: false,
    };
  },
  filters: {
    comma(val) {
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  created() {
    this.getItem();
  },
  methods: {
    getItem() {
      this.$apiGET(
        "/api/pay/breakdown/detail?merchantId=" + this.$route.query.id
      ).then(({ data }) => {
        this.item = data;
      });
    },
    payCancelCancel() {
      this.$apiPOST("/api/pay/cancel/cancel", {
        MerchantId: this.$route.query.id,
      }).then(({ data }) => {
        console.log(data);
        this.$router.go(-1);
        this.$store.commit(
          "updateError",
          "   .\n !"
        );
      });
    },
    payCancel() {
      this.$apiPOST("/api/pay/cancel", {
        MerchantId: this.$route.query.id,
      }).then(({ data }) => {
        this.viewCount = data.ReadCount;
        if (data.Is7Days == 1 && data.ReadCount == 0) {
          console.log(" ");
          this.payRealCancel(1);
        } else {
          this.popupState = true;
          console.log(" ");
        }
      });
    },
    payRealCancel(st) {
      this.popupState = false;
      this.$apiPOST("/api/pay/cancel/detail", {
        MerchantId: this.$route.query.id,
        CancelType: this.item.CancelType,
        CancelDetail: this.item.CancelDetail,
      }).then(({ data }) => {
        console.log(data);
        this.$router.go(-1);

        if (st == 1) {
          this.$store.commit(
            "updateError",
            "   .\n       \n."
          );
        } else {
          this.$store.commit(
            "updateError",
            "    ."
          );
        }
      });
    },
    goBack() {
      this.$router.go(-1);
    },
    goMenu(dest) {
      this.$router.push({
        name: dest,
      });
    },
  },
};
</script>
