<template>
  <div class="content">
    <div class="content-top center">
      <h2 class="tit"></h2>
    </div>
    <div class="input-wrap">
      <input
        type="text"
        class="input-text"
        placeholder=" (  )"
        v-model="userName"
      />
    </div>
    <div class="input-wrap">
      <input
        type="password"
        class="input-text"
        placeholder=""
        v-model="password"
      />
    </div>
    <div class="login-bottom">
      <label class="checkbox">
        <input type="checkbox" />
        <span class="check"></span>
        <span class="text"> </span>
      </label>
      <div class="right">
        <a @click="goMenu('FindId')"></a>
        <a @click="goMenu('FindPass')"> </a>
      </div>
    </div>
    <div class="btn-wrap">
      <button type="button" class="btn btn-full btn-black" @click="btnLogin">
        
      </button>
    </div>
    <div class="login-first">
       ?
      <a @click="goMenu('Join')"></a>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  components: {},
  data() {
    return {
      userName: "",
      password: "",
    };
  },
  methods: {
    btnLogin() {
      this.$apiPOST("/api/user/login", {
        UserName: this.userName,
        Password: this.password,
      }).then(({ data }) => {
        if (data.NeedDelivery) {
          this.$store.commit("updateState");
        }

        if (data.ContentReviewNeedCount) {
          this.$store.commit("updateReview");
        }
        this.goMenu("Home");
      });
    },
    goMenu(dest) {
      this.$router.push({
        name: dest,
        // query: { name: "Query  ", age: 2 },
      });
    },
  },
};
</script>
