        <colgroup>
          <col width="25%" />
          <col width="12%" />
          <col width="18%" />
          <col width="35%" />
          <col width="10%" />
        </colgroup>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th>&nbsp;</th>
        </tr>
        <tr v-for="item in itemList" :key="'c_i_' + item.Id">
