<script>
export default {
  name: "MyCard",
  components: {},
  data() {
    return {
      popupState: false,
      itemList: [],
    };
  },
  created() {
    this.getItemList();
  },
  methods: {
    getItemList() {
      this.$apiGET("/api/pay/cards").then(({ data }) => {
        this.itemList = data;
      });
    },
    goBack() {
      this.$router.go(-1);
    },
    goMenu(dest) {
      this.$router.push({
        name: dest,
      });
    },
  },
};
</script>
