<template>
  <div class="content">
    <div class="content-top center">
      <h2 class="tit"> </h2>
    </div>
    <div class="input-wrap">
      <input type="text" class="input-text" placeholder="" v-model="Name" />
    </div>
    <div class="input-wrap btn-exist">
      <input
        type="tel"
        class="input-text"
        placeholder="   "
        v-model="Contact"
        @keyup="phoneNumberMaks(Contact)"
      />
      <button type="button" class="btn input-btn" @click="getVerify">
         
      </button>
    </div>
    <div class="input-wrap">
      <input
        type="number"
        class="input-text"
        placeholder=" 6 "
        v-model="VerifyKey"
      />
    </div>
    <div class="btn-wrap">
      <button type="button" class="btn btn-full btn-black" @click="goResult">
         
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "FindId",
  components: {},
  data() {
    return {
      Name: "",
      Contact: "",
      VerifyKey: "",
    };
  },
  methods: {
    goResult() {
      this.$apiPOST("/api/user/find/username", {
        Name: this.Name,
        Contact: this.Contact,
        VerifyKey: this.VerifyKey,
      }).then(({ data }) => {
        this.$router.push({
          name: "FindIdResult",
          query: { Username: data.find.Username },
        });
      });
    },
    getVerify() {
      this.$apiGET(
        "/api/user/verify/username?Name=" +
          this.Name +
          "&Contact=" +
          this.Contact
      ).then(({ data }) => {
        console.log(data);
      });
    },
    phoneNumberMaks(value) {
      if (!value) {
        return "";
      }

      value = value.replace(/[^0-9]/g, "");

      let result = [];
      let restNumber = "";

      //    
      if (value.startsWith("02")) {
        //  02 
        result.push(value.substr(0, 2));
        restNumber = value.substring(2);
      } else if (value.startsWith("1")) {
        //    
        // 1xxx-yyyy
        restNumber = value;
      } else {
        //  3 
        // 0xx-yyyy-zzzz
        result.push(value.substr(0, 3));
        restNumber = value.substring(3);
      }

      if (restNumber.length === 7) {
        // 7   xxx-yyyy
        result.push(restNumber.substring(0, 3));
        result.push(restNumber.substring(3));
      } else {
        result.push(restNumber.substring(0, 4));
        result.push(restNumber.substring(4));
      }

      this.Contact = result.filter((val) => val).join("-");
    },
  },
};
</script>
